import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AccomodationComponent } from './accomodation/accomodation.component';
import { CelebrationsComponent } from './celebrations/celebrations.component';
import { FoodAndBeverageComponent } from './food-and-beverage/food-and-beverage.component';
import { LifestyleComponent } from './lifestyle/lifestyle.component';
import { WeddingsComponent } from './weddings/weddings.component';
import { OffersComponent } from './offers/offers.component';
import { SigninComponent } from './auth/signin/signin.component';
import { SignupComponent } from './auth/signup/signup.component';
import { HomeComponent } from './home/home.component';
import { BookingsComponent } from './bookings/bookings.component';
import { MybookingsComponent } from './mybookings/mybookings.component';
import { GuestpostComponent } from './guestpost/guestpost.component';
import { ThankyouComponent } from './thankyou/thankyou.component';
import { BookingsuccesfulComponent } from './bookingsuccesful/bookingsuccesful.component';


const routes: Routes = [
    { path: 'accomodation' , component: AccomodationComponent},
    {path : 'celebrations' , component: CelebrationsComponent},
    {path : 'food-and-beverage' , component: FoodAndBeverageComponent},
    {path : 'foodandbeverage' , redirectTo : 'food-and-beverage' , pathMatch: 'full'},
    {path: 'lifestyle' , component : LifestyleComponent},
    {path: 'weddings', component: WeddingsComponent},
    {path: 'offers', component: OffersComponent},
    {path: 'signin' , component: SigninComponent},
    {path: 'signup' , component: SignupComponent},
    {path: ''  , component: HomeComponent},
    {path: 'bookings' ,component:BookingsComponent},
    {path: 'mybookings' , component:MybookingsComponent},
    {path:'guestpost', component:GuestpostComponent},
    {path:'thankyou',component:ThankyouComponent},
     {path:'bookingsuccesful',component:BookingsuccesfulComponent}
];

@NgModule({
 imports: [RouterModule.forRoot(routes)],
 exports: [RouterModule]
})


export class AppRoutingModule { }
