import { Injectable } from '@angular/core';
import { AngularFireAction } from 'angularfire2/database';
import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable } from 'rxjs';
import * as firebase from 'firebase';
import { ok } from 'assert';
import { Post } from '../mybookings/post';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
newUser: any;
bookingdata: any;
currentuser: any;

getUserState(){
  return this.afAuth.authState;
}

datacollection:AngularFirestoreCollection<Post>
data:Observable<Post[]>;

private eventAuthError = new BehaviorSubject<string>("")
eventAuthError$ = this.eventAuthError.asObservable();

  constructor(
    private afAuth: AngularFireAuth,
    private db: AngularFirestore,
    private router: Router){
      
      this.data=this.db.collectionGroup(`Userss`).valueChanges();
    }

////////////////////////////////////////////////////////////////////////////////////////////////
    createUser(user){
      this.afAuth.auth.createUserWithEmailAndPassword(user.email, user.password)
      .then(userCredential => {
        this.newUser = user;

        userCredential.user.updateProfile({
          displayName: user.firstName + ' ' + user.lastName
        });

        this.insertUserData(userCredential)
        .then(() => {
            this.router.navigate(['/']);

        });
      })

      .catch(error => {
this.eventAuthError.next(error);
      })
    }

///////////////////////////////////////////////////////////////////////////////////
    insertUserData(userCredential: firebase.auth.UserCredential){
      return this.db.doc(`Users/${userCredential.user.uid}`).set({
        email: this.newUser.email,
        firstName: this.newUser.firstName,
        lastName: this.newUser.lastName,
        role: 'network user',
        checkindate: null,
        checkoutdate: null,
        rooms: null,
        adults: null,
        children: null
                  
      })
    }
//////////////////////////////////////////////////////////////////////////////

    logout(){
      return this.afAuth.auth.signOut()
      .then(() => {
        this.router.navigate(['/']);
      });

    }

    ///////////////////////////////////////////////////////////////////////////////////////


    login(email: string, password: string){
this.afAuth.auth.signInWithEmailAndPassword(email, password )
.catch(error => {
  this.eventAuthError.next(error);
})
.then(userCredential => {
  if (userCredential){
    this.router.navigate(['/']);
  }
})
    }
/////////////////////////////////////////////////////////////////////////////////////



createBooking(bookingdata){
  this.currentuser = bookingdata

  this.db.doc(`Users/${this.afAuth.auth.currentUser.uid}`).update({
    checkindate: this.currentuser.checkindate,
    checkoutdate: this.currentuser.checkoutdate,
    rooms: this.currentuser.rooms,
    adults: this.currentuser.adults,
    children: this.currentuser.children
    
  })
  .then(()=>this.router.navigate(['/']));


  }

/////////////////////////////////////////////////////////////////////////////////////////


getdata(){
  return this.data;
}

////////////////////////////////////////////////////////////////////////////////////////////



}



