import { Component, OnInit,AfterViewChecked } from '@angular/core';
import { AuthService } from '../auth/auth.service';
import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from 'rxjs';
import { User } from 'firebase';
import { AngularFirestore, AngularFirestoreCollection } from 'angularfire2/firestore';
import { AngularFireAuth } from 'angularfire2/auth';
import { Post } from './post';
import { MatDialog } from '@angular/material/dialog';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';

declare let paypal:any


@Component({
  selector: 'app-mybookings',
  templateUrl: './mybookings.component.html',
  styleUrls: ['./mybookings.component.css']
})
export class MybookingsComponent implements OnInit, AfterViewChecked {

user:firebase.User;
mydata:any[];
data:Post[];



addScript:boolean=false;
finalAmount:number=1;

userposts

  constructor(private af:AngularFireDatabase,
    private db: AngularFireDatabase,
    private auth: AuthService,
    private afAuth: AngularFireAuth,
    private afstore:AngularFirestore,
    public dialog:MatDialog,) {
      
 db.list('/Users').valueChanges()
.subscribe(mydata=>{
  this.mydata=mydata;
})

const userbooking=afstore.doc(`users/${auth.currentuser.uid}`)
this.userposts= userbooking .valueChanges()
     }

  


  ngOnInit(): void {
    this.auth.getUserState()
    .subscribe(user=>{
      this.user=user;
    });

    this.auth.getdata().subscribe(data=>{
      this.data=data;
    })
  }

  paypalConfig={
    env:'sandbox',
    client:{
      sandbox:'AcU1WMJLRBXWXPHuI4iB6dSLVsslg8PaML_wk9xyLXS6bkCYshmkLOr3W-yCXlaLlARmNfX20xXt2phd',
      production:'<prod_ID>'
    },
     commit: true,

    payment: (data,actions)=>{
      return actions.payment.create({
        payment:{
          transactions:[
            {amount:{total:this.finalAmount, currency:'USD'}}
          ]
        }
      });
    },
    onAuthorize: (data,actions)=>{
      return actions.payment.execute().then((payment)=>{
        //do something when payment succesful'
      })
    }

  };


  ngAfterViewChecked():void{

    if(!this.addScript){
      this.addPaypalScript().then(()=>{
   paypal.Button.render(this.paypalConfig,'#paypal-checkout-btn')
      });
    }

  }

  addPaypalScript(){
    this.addScript =true;
    return new Promise((resolve,reject)=>{
      let scripttagelement=document.createElement('script');
      scripttagelement.src='https://www.paypalobjects.com/api/checkout.js'; 
      scripttagelement.onload=resolve;
      document.body.appendChild(scripttagelement);
    })
  }

  
}