import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {HttpErrorResponse,} from '@angular/common/http';
import { AngularFirestore } from 'angularfire2/firestore';
import { AuthService } from '../auth/auth.service';
import { firestore } from 'firebase/app';
import { AngularFireAuth } from 'angularfire2/auth';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import {MatProgressSpinnerModule} from '@angular/material/progress-spinner';
import { MybookingsComponent } from '../mybookings/mybookings.component';
import { ThankyouComponent } from '../thankyou/thankyou.component';


@Component({
  selector: 'app-lifestyle',
  templateUrl: './lifestyle.component.html',
  styleUrls: ['./lifestyle.component.css']
})
export class LifestyleComponent implements OnInit {

imageURL:string;
event: any
busy:boolean=false;
  desc:string;
  uid:string



effects={
  effect1:'',
  effect2:'-/exposure/50/-/saturation/50/-/warmth/30/',
  effect3:'-/filter/vevera/200/',
  effect4:'-/filter/carris/150/'
}
  activeEffect:string=this.effects.effect1

  constructor(public http: HttpClient,
    public afstore:AngularFirestore,
    public user: AngularFireAuth,
    public router:Router,
    public dialog: MatDialog) { }

  ngOnInit(): void {
  }


filechanged(event){
  this.busy=true;

const files=event.target.files

const data=new FormData();
data.append('file',files[0]);
data.append('UPLOADCARE_STORE','1')
data.append('UPLOADCARE_PUB_KEY','6e06ddde699074c7cc53')

this.http.post('https://upload.uploadcare.com/base/',data)
.subscribe(event=>{
  console.log(event)

 // this.imageURL=event.file;
  this.busy=false
  console.log(this.imageURL)

}) 
}


setselected(effect:string){
this.activeEffect=this.effects[effect];
}


createPost(){
const image=this.imageURL;
const desc=this.desc;

this.afstore.doc(`Users/${this.user.auth.currentUser.uid}`).update({
  posts:firestore.FieldValue.arrayUnion(`${image}/${this.activeEffect}`)
})

this.afstore.doc(`Users/${image}`).set({
 desc,
 author: this.user.auth.currentUser.displayName,
 likes: [],
 effect:this.activeEffect
})



}

open(){

  const dialogRef = this.dialog.open(ThankyouComponent, {
    width: '225px',
    
  });

  setTimeout(() => {
    this.dialog.closeAll();
  }, 1500);
}

bookingclose(){
  this.dialog.closeAll();
}



}
