import { Component, OnInit , EventEmitter, Output} from '@angular/core';
import { AuthService } from 'src/app/auth/auth.service';
import { MybookingsComponent } from 'src/app/mybookings/mybookings.component';
import { MatDialog } from '@angular/material/dialog';
import { BookingsComponent } from 'src/app/bookings/bookings.component';
import { SignupComponent } from 'src/app/auth/signup/signup.component';
import { SigninComponent } from 'src/app/auth/signin/signin.component';
import { OffersComponent } from 'src/app/offers/offers.component';

@Component({
  selector: 'app-sidenav-list',
  templateUrl: './sidenav-list.component.html',
  styleUrls: ['./sidenav-list.component.css']
})
export class SidenavListComponent implements OnInit {

@Output() closeSideNavigation = new EventEmitter();
user:firebase.User;
  constructor(private auth:AuthService,
    public dialog :MatDialog) { }

  ngOnInit(): void {
    this.auth.getUserState()
    .subscribe(user=>{
      this.user=user;
    });
  }

  onToggleClose() {
    this.closeSideNavigation.emit();
  }


  logout(){
    this.auth.logout();

  }


  openDialog() {
    const dialogRef = this.dialog.open(MybookingsComponent, {
      width: '550px',
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }


  openDialogbooking() {
    const dialogRef = this.dialog.open(BookingsComponent, {
      width: '600px',
    });
         
    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }

  bookingclose(){
    this.dialog.closeAll();
  }

  opensignup(){
    const dialogRef = this.dialog.open(SignupComponent, {
      width: '550px',
    });
         
    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }

  openDg() {
    const dialogRef = this.dialog.open(SigninComponent, {
      width: '500px',
    });
         
    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }

  openoffer(){
    const dialogRef = this.dialog.open(OffersComponent, {
      width: '800px',
      height:'500px'
    });
         
    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);
    });
  }


}

  


