import { Component, OnInit } from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { LifestyleComponent } from '../lifestyle/lifestyle.component';
import { Router } from '@angular/router';
import { AuthService } from '../auth/auth.service';

@Component({
  selector: 'app-guestpost',
  templateUrl: './guestpost.component.html',
  styleUrls: ['./guestpost.component.css']
})
export class GuestpostComponent implements OnInit {

  constructor(public dialog:MatDialog,
    public router:Router,
    private auth:AuthService) { }

user:firebase.User;

  ngOnInit(): void {
    this.auth.getUserState()
    .subscribe(user=>{
      this.user=user;
    });
  }


  openDialogcreatepost() {
    const dialogRef = this.dialog.open(LifestyleComponent, {
      width: '400px',
      height:'600px',
    });
         
    dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);

    });
  }
  
  bookingclose(){
    this.dialog.closeAll();
    this.router.navigate(['/mybookings']);
      
  }
  }


